;\r\n}\r\n\r\n%reset-style {\r\n    margin: 0;\r\n    padding: 0;\r\n    font: inherit;\r\n    color: inherit;\r\n    text-decoration: none;\r\n    border: none;\r\n    background: none;\r\n}\r\n\r\n/*specific styles*/\r\n\r\n%main-margin {\r\n    margin-top: 59px;\r\n    margin-bottom: 63px;\r\n}\r\n\r\n%wheel-center-anim {\r\n    transition: transform 7s cubic-bezier(0.3, 0.8, 0.3, 1);\r\n}\r\n\r\n/*colors & gradients*/\r\n\r\n$gradient-body: linear-gradient(180deg, #2477A0, #481C7F);\r\n$gradient-button: linear-gradient(180deg, #FFC700, #E4A400);\r\n$gradient-modal: linear-gradient(180deg, #5977C3, #54268D);\r\n$gradient-modal-border: linear-gradient(140deg, #789CD2, #5977C3, #54268D);\r\n\r\n$text-purple-stroke: 1px 1px  #490082, -1px -1px  #490082, 1px -1px  #DB00FF, -1px 1px  #DB00FF;\r\n  \r\n/*shadows*/\r\n\r\n//default\r\n$inner-shadow-top: inset 0px -6px 4px rgba(0, 0, 0, 0.2);\r\n$inner-shadow-bottom: inset 0px 4px 4px rgba(255, 255, 255, 0.5);\r\n$outer-shadow: 0px 6px 12px rgba(27, 26, 72, 0.5);\r\n$def-shadow-btn: $inner-shadow-top, $inner-shadow-bottom, $outer-shadow;\r\n\r\n//hover\r\n$outer-hover-shadow: 0px 6px 12px rgba(38, 108, 154, 1);\r\n$hover-shadow-btn: $inner-shadow-top, $inner-shadow-bottom, $outer-hover-shadow;\r\n\r\n//active\r\n$outer-active-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);\r\n$active-shadow-btn: $outer-active-shadow;\r\n\r\n\r\n//modal-shadow\r\n\r\n$modal-shadow: 0px 9px 60px #0E1155;\r\n\r\n","@font-face {\r\n    font-family: 'Geologica Bold';\r\n    src: url('./assets/fonts/Geologica-Bold.ttf');\r\n}\r\n","@use './variables' as *;\r\n@use './fonts' as *;\r\n@use './mixins' as *;\r\n\r\n\r\n* {\r\n    box-sizing: border-box;\r\n}\r\n\r\nbody {\r\n    @extend %base-reset;\r\n    @extend %base-font;\r\n\r\n    @include flex(row, center);\r\n    text-shadow: $text-purple-stroke;\r\n\r\n    background: $gradient-body;\r\n    height: 100vh;\r\n\r\n\r\n  main {\r\n        @include flex(column, space-between, center);\r\n        @extend %main-margin;\r\n    }\r\n}\r\n\r\n.reset-style {\r\n    @extend %reset-style;\r\n}\r\n\r\n#rotate-btn {\r\n    @include button(409px);\r\n\r\n    text-shadow: $text-purple-stroke;\r\n}\r\n\r\n","@use './variables' as *;\r\n\r\n/*POSITIONING*/\r\n\r\n@mixin flex($direction: row, $justify: flex-start, $align: stretch) {\r\n    display: flex;\r\n    flex-direction: $direction;\r\n    justify-content: $justify;\r\n    align-items: $align;\r\n}\r\n\r\n@mixin translate($x: null, $y: null) {\r\n    @if $x != null and $y != null {\r\n        transform: translate($x, $y);\r\n    } @else if $x != null {\r\n        transform: translateX($x);\r\n    } @else if $y != null {\r\n        transform: translateY($y);\r\n    }\r\n}\r\n\r\n/*SIZES*/\r\n\r\n@mixin wh($w, $h) {\r\n    width: $w;\r\n    height: $h;\r\n}\r\n\r\n/*BGC & COLORS*/\r\n\r\n@mixin gradient($deg: 180deg, $colors...) {\r\n    background: linear-gradient($deg, $colors);\r\n}\r\n\r\n/*SPECIFIC*/\r\n\r\n@mixin button($width, $height: 72px, $font-size: 32px) {\r\n    @include wh($width, $height);\r\n\r\n    font-size: $font-size;\r\n    position: relative;\r\n    border-radius: 999px;\r\n    background: $gradient-button;\r\n    box-shadow: $def-shadow-btn;\r\n    transition: box-shadow 0.3s ease-in-out;\r\n\r\n    cursor: pointer;\r\n\r\n    &:hover {\r\n        box-shadow: $hover-shadow-btn;\r\n    }\r\n\r\n    &:active {\r\n        box-shadow: $outer-active-shadow;\r\n    }\r\n}\r\n\r\n/*wheel*/\r\n\r\n@mixin wheel-star-ovrl($trX: -50%, $trY: -50%) {\r\n    @include translate($trX, $trY);\r\n\r\n    position: absolute;\r\n    z-index: 1;\r\n    opacity: 60%;\r\n}\r\n\r\n@mixin wheel-arrow($top) {\r\n    position: absolute;\r\n    z-index: 3;\r\n\r\n    top: $top;\r\n}\r\n\r\n@mixin wheel-sector($width) {\r\n    width: $width;\r\n    aspect-ratio: 1/1.3;\r\n    background-repeat: no-repeat;\r\n    \r\n    position: absolute;\r\n    top: 100%;\r\n    left: 50%;\r\n\r\n    transform-origin: bottom;\r\n}\r\n\r\n@mixin wheel-sector-text($font-size, $top) {\r\n    @include translate(-50%);\r\n\r\n    position: absolute;\r\n    top: $top;\r\n    left: 50%;\r\n\r\n    font-size: $font-size;\r\n}\r\n\r\n/*MODAL*/\r\n\r\n@mixin modal-overlay {\r\n    @include wh(100vw, 100vh);\r\n    @include flex(row, center, center);\r\n\r\n    position: fixed;\r\n    top: 0; \r\n    left: 0;\r\n\r\n    background: rgba(0, 0, 0, 0.5); \r\n    backdrop-filter: blur(4px);    \r\n    \r\n    z-index: 1000;\r\n\r\n    opacity: 0;\r\n    visibility: hidden;\r\n\r\n    transition: opacity 1s ease, visibility 1s ease;\r\n}\r\n\r\n@mixin modal-content-spec($width, $height, $p-t, $p-b, $b-radius, $b-inset) {\r\n    @include flex(column, space-between, center);\r\n    @include wh($width, $height);\r\n\r\n    padding-top: $p-t;\r\n    padding-bottom: $p-b;\r\n    border-radius: $b-radius;\r\n    background: $gradient-modal;\r\n\r\n    position: relative;\r\n\r\n    &::before {\r\n        content: '';\r\n        position: absolute;\r\n        inset: $b-inset;\r\n        background: $gradient-modal-border;\r\n        border-radius: $b-radius + 10px; \r\n        \r\n        z-index: -1;\r\n    }\r\n}\r\n\r\n@mixin modal-active {\r\n    display: flex;\r\n    opacity: 1;\r\n    visibility: visible;\r\n}\r\n\r\n@mixin modal-text($font-size, $letter-spc: 0) {\r\n    font-size: $font-size;\r\n    letter-spacing: $letter-spc;\r\n}","@use './variables' as *;\r\n@use './fonts' as *;\r\n@use './mixins' as *;\r\n\r\n#wheel-container {\r\n    @include flex(row, center);\r\n    position: relative;\r\n\r\n    #wheel-star-overlay {\r\n        @include wheel-star-ovrl();\r\n    }\r\n\r\n    #wheel-center {\r\n        @include translate(-50%, -50%);\r\n        position: absolute;\r\n        top: 50%;\r\n        left: 50%;\r\n\r\n        z-index: 2;\r\n    }\r\n\r\n    #wheel-border {\r\n        z-index: 2\r\n    }\r\n\r\n    #wheel-arrow {\r\n        @include wheel-arrow(-35px);\r\n    }\r\n}\r\n\r\n#wheel-inner{\r\n    @incl